main{
    width: calc(100% - 15rem);
    background-color: aqua;
    height: 100%;
    overflow-y: hidden;
}


/* width */
nav::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  nav::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  nav::-webkit-scrollbar-thumb {
    background: red; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  nav::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }


  .nav-option-text{
    color:white;
    padding-left: 1rem;
}

.nav-option-icon{
    color:white;
    padding-left: 1rem;
}

.nav-option{
    display:flex;
    padding-left: 2rem;
    padding: 1rem 0;
    border:1px solid blue;;
}

.nav-option:hover{
    background-color: grey;
    cursor: pointer;
}

.tabs-container{
    width: 100%;
    height:2.5rem;
    background-color: blueviolet;
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
}


/* width */
.tabs-container::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  
  /* Track */
  .tabs-container::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  .tabs-container::-webkit-scrollbar-thumb {
    background: red; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  .tabs-container::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }

  .tab{
    min-width: 10%;
    max-width: 10%;
    height: 100%;
    padding-left:0.5rem;
    background-color: darkgoldenrod;
    text-align: center;
    border-left: 1px solid black;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    padding-top: 0.3rem;
}


.tab:last-child{
    border-right: 1px solid black;
}


.tab-text{
    width: 100%;
    background-color: yellow;
    padding:0.4rem 0;
    max-width: 100%;
    overflow: hidden;
}

.tab-x{
    padding-top: 0.5rem;
    padding-left: 0;
    width: 20%;
    overflow: hidden;
    cursor: pointer;
}

.tab-content{
    width: 80%;
    cursor: pointer;
}

.leftNav{
  background-color:black;
  width:15rem;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}



@media screen and (max-width:991px) {
  .leftNav{
    max-width: 3rem;
  }

  .leftNav-active{
    max-width: 15rem;
  }

  .nav-option-text-active{
    display: block !important;
  }

  main{
    width:calc(100% - 3rem);
  }
  .nav-option-text{
    display: none;
  }
}

